@import url('https://fonts.googleapis.com/css?family=Oswald&display=swap');

body {
  background-color: #F5F2EC;
  color: black;
  padding-top: 10vw;
}

h2 {
  font-size: 22px;
  font-weight: 400;
  line-height: 1.4;
  text-transform: none;
  margin-top: 15px;
  margin-bottom: 15px;
  font-family: 'Oswald', sans-serif;
}

h4 {
  margin: 5px;
  padding: 0;
  letter-spacing: 1px;
  font-size: 12px;
}

h5 {
  font-size: 10px;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 1px;
  margin:0;
}

h2 b {
  color:green;
}

.wrapper {
  margin-top:10px;
  display: flex;
  flex-wrap: wrap;
  align-content: center;
  height: 100%;
}

.small-12 {
  flex-basis: 50%;
}

.garage {
  padding: 2%;
  width: 13%;
}

.tree {
  padding: 1% 2%;
  width: 13%;
}

.equal {
  font-size: 60px;
  text-align: center;
  margin: 40px 0;
}

.column {
  width: 100%;
}

.ui.basic.button {
  font-family: 'Oswald', sans-serif;
  line-height: 30px;
  font-size: 14px;
  padding: 5px 18px;
  border: 1px solid black;
  border-radius: 0;
  margin: 30px 0;
}

.ui.basic.button .tree {
  width:22px;
  float: left;
  margin-right: 5px;
  padding: 0;
}

.ui.basic.button .garage {
  width:23px;
  float: left;
  margin-right: 5px;
  margin-top:4px;
  padding: 0px;
}


@media only screen and (min-width: 768px) {
  h2 {
    font-size: 24px;
  }
  .ui.container {
    width: 600px;
  }

  .garage {
    padding: 3%;
    width: 20%;
  }
  .tree {
    padding: 1% 2%;
    width: 14%;
  }  
}

@media only screen and (min-width: 480px) {
  h5 {
    font-size: 12px;
  }
  .medium-6 {
    width: 50%;
  }
  .medium-4 {
    width: 33%;
  }
  .medium-3 {
    width: 25%;
  }
  .medium-2 {
    width: 17%;
  }
  .cars {
    width: 75%;
  }
  .garage {
    padding: 3%;
    width: 30%;
  }
}
